import styled from "styled-components";

export const StyleSocialIcons = styled.div`
display: flex;
align-items: center;
justify-content: center;

li{
    list-style: none;
}

a{
    
    border: 2px solid green;
    border-radius: 50%;
    color: #fff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    height: 40px;
    width: 40px;
    text-decoration: none;
}
`